@mixin port_item_before {
  position: absolute;
  bottom: 0;
  line-height: 2.5em;
  background: #ffffffc0;
  width: 100%;
}

$blue: #2355da;

*,
body {
  margin: 0;
  padding: 0;
}
.home_wrapper {
  overflow-x: hidden;

  .home_header {
    padding: 2em 3em;
    display: flex;

    .heading_block {
      display: grid;
      align-content: center;
    }

    img {
      width: 90%;
      margin-left: 5%;
    }

    h1 {
      font-size: 2.8em;
      line-height: 1.7em;
    }
  }
  @media screen and (max-width: 768px) {
    .home_header {
      display: grid;
    
      .heading_block {
        order: 2;
        padding: 0;
      }
  
      h1 {
        margin: 1em;
        font-size: 2em;
        text-align: center;
      }
    }
  }
  // =========================
  .we_love_we_help_block {
    display: grid;
    text-align: center;
    font-size: 1.1em;

    .we_love_block {
      background: #f9fafd;
      padding: 4em 2em;

      p {
        margin-bottom: 4em;
      }

      p a {
        color: $blue;
        font-weight: 500;
      }

      .about_us_button {
        background: $blue;
        color: #fff;
        border-radius: 2em;
        border: 2px solid $blue;
        padding: 0.5em 2.5em;
        text-decoration: none;
        font-size: 1.1em;

        :hover {
          background: #fff;
          color: $blue;
        }
      }

      h2 {
        font-size: 3em;
        font-weight: 400;
        line-height: 1.5em;

        b {
          font-weight: 700;
        }
      }
    }
    .we_help_block {
      background: $blue;
      color: #fff;
      padding: 4em 2em;

      h2 {
        margin-bottom: 1em;
        font-weight: bold;
        font-size: 2.5em;
      }
    }
  }
  // ============================
  .our_services_wrap {
    padding: 4em 2em;

    ul {
      margin-top: 2em;
      list-style: none;
    }

    h2 {
      font-weight: 700;
    }

    li {
      div {
        img {
          width: 5em;
        }

        h3 {
          font-weight: 400;
          width: 60%;
          margin: 1em auto;
        }

        p {
          line-height: 1.9em;
          font-size: 1.3em;
        }
      }
    }
  }
  @media screen and (min-width: 769px) {
    .our_services_wrap {
      ul {
        li {
          div {
            display: grid;
            grid-auto-flow: column;
            grid-gap: 1em;
            justify-content: start;

            img {
              width: 4em;
            }

            h3 {
              text-align: start;
              width: 80%;
              margin: 0 0 0.7em;
            }
          }
          p {
            padding-left: 5em;
            text-align: start;
            font-size: 1.07em;
          }
        }
      }
    }
  }
  // ====================
  .portflio_wrapper {
    padding: 3.5em 0;

    ul {
      list-style: none;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr;
      grid-gap: 0.3em;
      justify-content: center;
      margin-bottom: 3em;

      li {
        position: relative;

        &:first-child:hover::before {
          content: "IG-Cleaner inner";
          @include port_item_before;
        }
        &:nth-child(2):hover::before {
          content: "Life Listr inner";
          @include port_item_before;
        }
        &:nth-child(3):hover::before {
          content: "Nomadic Wall inner";
          @include port_item_before;
        }
        &:nth-child(4):hover::before {
          content: "GoTeach inner";
          @include port_item_before;
        }
        &:nth-child(5):hover::before {
          content: "Keezi Keyboards Free inner";
          @include port_item_before;
        }
        &:nth-child(6):hover::before {
          content: "hidephoto inner";
          @include port_item_before;
        }

        img {
          width: 100%;
        }
      }
    }

    h2 {
      margin-bottom: 0.7em;
    }

    .check_out_button {
      text-decoration: none;
      background: $blue;
      color: #fff;
      border-radius: 2em;
      padding: 0.4em 2em 0.6em;
      font-size: 1.2em;

      &:hover {
        transition: 0.5s;
        background: #fff;
        color: #ff9100;
        border: 1px solid #ff9100;
      }
    }
  }
  // ===============================
  .features_wrap {
    padding: 4em 1em;

    h2 {
      font-weight: 700;
      margin-bottom: 1em;
    }

    .subheading {
    }

    .features_list {
      padding: 1em 2.5em;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-gap: 1em;
      list-style: none;
      margin-top: 3em;

      li {
        div {
          display: grid;
          grid-auto-flow: column;
          grid-gap: 2em;
          justify-content: start;

          img {
            width: 5em;
          }

          h3 {
            font-weight: 600;
            text-align: start;
            width: 60%;
          }
        }

        p {
          margin-left: 7em;
          text-align: start;
        }
      }
    }
    @media screen and (max-width: 768px) {
      .features_list {
        padding: 0 1em;
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 2em;

        li {
          div {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 3em;
            justify-items: center;

            h3 {
              width: 100%;
              font-size: 1.5em;
              text-align: center;
            }

            img {
              width: 4.5em;
            }
          }

          p {
            margin-left: 0;
            text-align: center;
          }
        }
      }
    }
  }
  // =========================
  .technologies_wrap {
    padding: 2em 1em 4em 1em;
    background: #f9fafd;

    h2 {
      font-weight: 700;
      margin: 2em 0;
    }

    .slide {
      margin: 0 auto;
      width: 90%;
    }
  }
  // =====================
  .industries_wrap {
    padding: 3em 1em;

    h2 {
      margin: 1em 0;
    }

    .industries_list {
      padding-top: 2em;
      display: grid;
      grid-gap: 1em;

      div {
        background: #f9fafd;
        padding: 2em;

        h3 {
          font-size: 1.5em;
          margin-top: 1em;
        }

        &:hover {
          background: #d1d1d1;
          transition: 0.5s;
        }
      }
    }
  }
  @media screen and (min-width: 769px) {
    .industries_wrap {
      h2 {
        font-size: 3em;
        font-weight: 700;
      }

      .subheading {
        margin-top: -1em;
      }

      .industries_list {
        margin-top: 1em;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-template-rows: 1fr 1fr;
      }
    }
  }
  // ==========================
  .our_values_wrap {
    margin-top: 2em;
    padding: 4em 1em;
    background: #f9fafd;

    h2 {
      font-weight: 700;
      margin: 1em 0;
    }

    .subheading {
      font-size: 0.9em;
    }

    .values_list {
      margin-top: 3em;
      display: grid;
      grid-auto-flow: row;
      grid-gap: 2em;
      list-style: none;

      li {
        h3 {
          margin: 1em 0;
          font-weight: 700;
        }
      }
    }
  }
  @media screen and (min-width: 769px) {
    .our_values_wrap {
      h2 {
        font-size: 3em;
        margin: 1em 0 0.5em 0;
      }

      .values_list {
        display: flex;
      }
    }
  }
  // =============
  .form_wrap {
    background: #f9fafd;
    padding: 3em 1em;

    input,
    textarea {
      margin-bottom: 1em;
      height: 3em;
      border: 1px solid #dbdbdb;
    }
    textarea {
      padding-top: 0.7em;
    }
    input[type="submit"] {
      border-radius: 2em;
      border: 1px solid #3f3fff;
      color: #3f3fff;
      background: none;
      padding: 0 3em;
      font-weight: 500;

      &:hover {
        transition: 0.5s;
        background: $blue;
        color: #fff;
      }
    }

    .small_inputs_group {
      padding: 0 1em;
    }

    .big_inputs_group {
      padding: 0 1em;
    }
  }
  @media screen and (min-width: 769px) {
    .form_wrap {
      h3 {
        font-size: 2.3em;
        font-weight: 700;
      }
      .small_inputs_group {
        input {
          width: 35%;
        }
      }
    }
  }
}
